import logo200Image from 'assets/img/logo/logo_200.png';
import PropTypes from 'prop-types';
import React from 'react';
import { Button, Form, FormGroup, Input, Label } from 'reactstrap';
import NotificationSystem from 'react-notification-system';
import {

  MdWarning,
} from 'react-icons/md';
import { NOTIFICATION_SYSTEM_STYLE } from 'utils/constants';
class AuthForm extends React.Component {
  state={
    phone:'',
    password:'',
    confirmPassword:'',
    recommendationCode:'',
    confirmPWDErr:false,
    inProgress:false,
    agree:false
  }
  get isLogin() {
    return this.props.authState === STATE_LOGIN;
  }

  get isSignup() {
    return this.props.authState === STATE_SIGNUP;
  }

  changeAuthState = authState => event => {
    event.preventDefault();

    this.props.onChangeAuthState(authState);
  };

  handleSubmit = event => {

    this.setState({
      inProgress:true
    })
    event.preventDefault();
    if(this.props.authState === STATE_LOGIN){
      fetch("/api/login", {
        "method": "POST",
        "headers": {
          "content-type": "application/json",
        },
        "body": JSON.stringify({
          phone: this.state.phone,
          password: this.state.password
        })
      })
      .then(response =>{ 
        if(response.status<400){
          response.json().then(res=>{
            localStorage.setItem("auth",JSON.stringify(res));
            this.setState({
              inProgress:false
            });
            this.props.onSubmit();
          });
  
        }else{
          response.json().then(res=>{
            this.setState({
              inProgress:false
            });
            console.log(res.error);
            this.notificationSystem.addNotification({
                  title: <MdWarning />,
                  message:res.error,
                  level: 'info',
                });
          });
          
        }
  
      });
      
    }else{
      if(!this.state.confirmPWDErr && this.state.phone!='' && this.state.recommendationCode!='' && this.state.agree){
        fetch("/api/signup", {
          "method": "POST",
          "headers": {
            "content-type": "application/json",
          },
          "body": JSON.stringify({
            phone: this.state.phone,
            password: this.state.password,
            recommendationCode:this.state.recommendationCode
          })
        })
        .then(response =>{ 
          if(response.status<400){
            response.json().then(res=>{
              localStorage.setItem("auth",JSON.stringify(res));
              this.setState({
                inProgress:false
              });
              this.props.onSubmit();
            });
    
          }else{
            response.json().then(res=>{
              this.setState({
                inProgress:false
              });
              this.notificationSystem.addNotification({
                    title: <MdWarning />,
                    message:res.error,
                    level: 'info',
                  });
            });
          }
    
        });
      }
      
    }
    
  };
  changePhone= event =>{
    this.setState({
      phone: event.target.value
    })
  }
  changePassword= event =>{
    if(event.target.value!=this.state.confirmPassword){
      this.setState({
        confirmPWDErr:true,
        password:event.target.value
      })
    }else{
      this.setState({
        confirmPWDErr:false,
        password:event.target.value
      })
    }
  }
  changeConfirmPassword= event =>{
    if(event.target.value!=this.state.password){
      this.setState({
        confirmPWDErr:true,
        confirmPassword:event.target.value
      })
    }else{
      this.setState({
        confirmPWDErr:false,
        confirmPassword:event.target.value
      })
    }
    

  }
  changeRecommendationCdoe= event =>{
 
    this.setState({
      recommendationCode: event.target.value
    })
  }
  renderButtonText() {
    const { buttonText } = this.props;

    if (!buttonText && this.isLogin) {
      return 'Login';
    }

    if (!buttonText && this.isSignup) {
      return 'Signup';
    }

    return buttonText;
  }
  onChangeAgree=()=>{
    console.log(this.state.agree);
    this.setState({
      agree:!this.state.agree
    });
  }
  render() {
    const {
      showLogo,
      usernameLabel,
      usernameInputProps,
      passwordLabel,
      passwordInputProps,
      confirmPasswordLabel,
      confirmPasswordInputProps,
      children,
      onLogoClick,
      recommendationCodeLabel,
  recommendationCodeInputProps,
    } = this.props;

    return (
      <Form onSubmit={this.handleSubmit}>
        {showLogo && (
          <div className="text-center pb-4">
            <img
              src={logo200Image}
              className="rounded"
              style={{ width: 60, height: 60, cursor: 'pointer' }}
              alt="logo"
              onClick={onLogoClick}
            />
          </div>
        )}
        <FormGroup>
          <Input {...usernameInputProps} onChange={this.changePhone} />
        </FormGroup>
        <FormGroup>
          <Input {...passwordInputProps} onChange={this.changePassword} />
        </FormGroup>
        {this.isSignup && (
          <FormGroup>
            <Input {...confirmPasswordInputProps} onChange={this.changeConfirmPassword}  />
            {
              this.state.confirmPWDErr && (
                <span className='text-danger'>password not match</span>
              )
            }
          </FormGroup>

        )}
               {/* {this.isSignup && (
         
          <FormGroup>
          <Label for={verificationCodeLabel}>{verificationCodeLabel}</Label>
          <Input {...verificationCodeInputProps} />
        </FormGroup>
        
        )} */}
               {this.isSignup && (
         
        <FormGroup>
        <Input {...recommendationCodeInputProps} onChange={this.changeRecommendationCdoe} />
      </FormGroup>
        )}
        { this.isSignup && (
        <FormGroup check>
          <Label check>
            <Input type="checkbox" value={this.state.agree} onChange={this.onChangeAgree} />{' '}
            'Agree the terms and policy'
          </Label>
        </FormGroup> 
        )}
        <hr />
        <Button
          size="lg"
          className="bg-gradient-theme-left border-0"
          block
          onClick={this.handleSubmit}>
          {this.renderButtonText()}
        </Button>

        { <div className="text-center pt-1">
          <h6>or</h6>
          <h6>
            {
            this.state.inProgress ? (
                <a href="" >
                Loading...
                </a>
              ) : (
                this.isSignup ? (
                  <a href="#login" onClick={this.changeAuthState(STATE_LOGIN)}>
                    Login
                  </a>
                ) : (
                  <a href="#signup" onClick={this.changeAuthState(STATE_SIGNUP)}>
                    Signup
                  </a>
                )
              )
            }
        
          </h6>
        </div> }

        {children}
        <NotificationSystem
          dismissible={false}
          ref={notificationSystem =>
            (this.notificationSystem = notificationSystem)
          }
          style={NOTIFICATION_SYSTEM_STYLE}
        />
      </Form>
    );
  }
}

export const STATE_LOGIN = 'LOGIN';
export const STATE_SIGNUP = 'SIGNUP';

AuthForm.propTypes = {
  authState: PropTypes.oneOf([STATE_LOGIN, STATE_SIGNUP]).isRequired,
  showLogo: PropTypes.bool,
  usernameLabel: PropTypes.string,
  usernameInputProps: PropTypes.object,
  passwordLabel: PropTypes.string,
  passwordInputProps: PropTypes.object,
  confirmPasswordLabel: PropTypes.string,
  confirmPasswordInputProps: PropTypes.object,
  // verificationCodeLabel: PropTypes.string,
  // verificationCodeInputProps: PropTypes.object,
  recommendationCodeLabel: PropTypes.string,
  recommendationCodeInputProps: PropTypes.object,
  onLogoClick: PropTypes.func,
};

AuthForm.defaultProps = {
  authState: 'LOGIN',
  showLogo: true,
  usernameLabel: 'Phone',
  usernameInputProps: {
    type: 'number',
    placeholder: 'your phone number',
  },
  passwordLabel: 'Password',
  passwordInputProps: {
    type: 'password',
    placeholder: 'your password',
  },
  confirmPasswordLabel: 'Confirm Password',
  confirmPasswordInputProps: {
    type: 'password',
    placeholder: 'confirm your password',
  },
  recommendationCodeLabel: 'Recommendation Code',
  recommendationCodeInputProps: {
    type: 'string',
    placeholder: 'recommendation code',
  },
  onLogoClick: () => {},
};

export default AuthForm;
